.uppercase {
  text-transform: uppercase;
}

.italic {
  font-style: italic;
}

.noitalic {
  font-style: normal;
}

.ff-tt-chocolates {
  font-family: "Tt Chocolates", sans-serif;
}

.ff-tt-norms-pro {
  font-family: "Tt Norms Pro Serif", serif;
}

html.is-locked {
  overflow: hidden;
}

*:focus-visible {
  outline: 2px dashed var(--pink);
}

.indirm-line {
  z-index: 999;
}

.ui-slider {
  position: relative;
}
.ui-slider-horizontal .ui-slider-range {
  height: 100%;
  position: absolute;
}
.ui-slider-horizontal .ui-slider-handle {
  top: 50%;
  translate: -50% -50%;
}

.empty-content {
  grid-column: -1/1;
  padding-block: 20rem;
  font-size: 20rem;
  line-height: 1.25;
  text-align: center;
}

@media (prefers-reduced-motion: reduce) {
  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
.breadcrumbs {
  --column-gap: 10rem;
  --color: #fff;
  padding: 0;
  margin: 0;
  display: flex;
  align-items: center;
  column-gap: var(--column-gap, 10rem);
  color: var(--color);
  list-style: none;
}
.breadcrumbs__item {
  display: flex;
  align-items: center;
  column-gap: var(--column-gap, 10rem);
  font-size: 12rem;
  line-height: 1;
  text-transform: uppercase;
  color: inherit;
}
.breadcrumbs__item a {
  text-decoration: none;
  color: inherit;
}
.breadcrumbs__item:not(:last-child)::after {
  content: "";
  margin: 0;
  width: 2rem;
  height: 2rem;
  background-color: var(--color);
  rotate: 45deg;
}
.breadcrumbs--single-dress {
  --color: #000000;
}
.breadcrumbs--single-dress .breadcrumbs__item:not(:last-child)::after {
  border-radius: 100%;
}

.menu-link {
  display: inline-flex;
  flex-direction: column;
  row-gap: 4rem;
  color: #000;
  font-size: 12rem;
  line-height: 1;
  font-weight: 500;
  text-transform: uppercase;
  text-decoration: none;
}
.menu-link::after {
  content: "";
  width: 100%;
  height: 1rem;
  background-color: var(--pink);
  scale: 0 1;
  transform-origin: left center;
  transition-property: scale;
  transition-duration: 0.5s;
  transition-timing-function: ease;
}
.menu-link.w--current::after, .menu-link.active::after {
  scale: 1 1;
}
@media (hover: hover) {
  .menu-link:hover::after {
    scale: 1 1;
  }
  .menu-link:focus::after {
    scale: 1 1;
  }
}
@media (hover: none) {
  .menu-link:active::after {
    scale: 1 1;
  }
}

.dialog {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100dvh;
  z-index: 1000;
}
.dialog__close {
  --top: 20rem;
  --right: 20rem;
  position: absolute;
  top: var(--top);
  right: var(--right);
  width: 20rem;
  height: 20rem;
  margin: 0;
  padding: 0;
  border: none;
  background: none;
  color: #646970;
  z-index: 1000;
  cursor: pointer;
  transition: color 0.1s ease-in-out, background 0.1s ease-in-out;
}
html:has(#wpadminbar) .dialog__close {
  --top: calc(32px + 20rem);
}
.dialog__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
}
.dialog.is-open {
  display: block;
}

.dialog-card {
  position: absolute;
  top: 0;
  right: 0;
  padding: 54rem 20rem;
  width: 100%;
  max-width: 360rem;
  height: 100dvh;
  background-color: #fff;
  display: flex;
  flex-direction: column;
  row-gap: 36rem;
}
.dialog-card__title {
  margin: 0;
  font-size: 26rem;
  line-height: 1;
  font-weight: 300;
}
.dialog-card__subtitle {
  margin-top: 20rem;
  font-size: 16rem;
  line-height: 20rem;
}
.dialog-card__link {
  margin-top: 15rem;
}
.dialog-card__body {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  row-gap: 20rem;
}
.dialog-card__footer {
  display: flex;
  flex-direction: column;
}
html:has(#wpadminbar) .dialog-card {
  padding-top: calc(32px + 54rem);
}

.fitting-form {
  display: grid;
  row-gap: 20rem;
}
.fitting-form__back {
  padding: 10rem;
  position: absolute;
  top: 10rem;
  left: 10rem;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: transparent;
  transition: all 0.3s ease;
}
@media (hover: hover) {
  .fitting-form__back:hover {
    opacity: 0.6;
  }
  .fitting-form__back:focus {
    opacity: 0.6;
  }
}
@media (hover: none) {
  .fitting-form__back:active {
    opacity: 0.6;
  }
}
html:has(#wpadminbar) .fitting-form__back {
  top: calc(32px + 10rem);
}
.fitting-form__back:disabled {
  display: none;
  pointer-events: none;
}
.fitting-form__step {
  display: grid;
  row-gap: 40rem;
}
.fitting-form__step[hidden] {
  display: none;
}
.fitting-form__group {
  display: flex;
  flex-direction: column;
  row-gap: 20rem;
}
.fitting-form__group-header {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.fitting-form__group-heading {
  font-size: 16rem;
  line-height: 1;
  text-transform: uppercase;
}
.fitting-form__group-body {
  display: flex;
  flex-direction: column;
  row-gap: 10rem;
}
.fitting-form__group-footer {
  font-size: 12rem;
  line-height: 16rem;
  font-weight: 500;
  text-transform: uppercase;
  color: rgba(0, 0, 0, 0.3);
  text-align: center;
}
.fitting-form__columns {
  display: grid;
  grid-template-columns: repeat(3, minmax(100rem, 1fr));
  column-gap: 10rem;
}
.fitting-form__columns-error {
  grid-column: 1/-1;
}
.fitting-form__columns.is-loading {
  opacity: 0.5;
  pointer-events: none;
}
.fitting-form__actions {
  display: flex;
  justify-content: space-between;
  align-items: center;
  column-gap: 66rem;
}
.fitting-form__actions-button {
  padding: 0;
  width: 30rem;
  height: 30rem;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: transparent;
  transition: opacity 0.3s ease;
}
.fitting-form__actions-button--prev {
  rotate: 180deg;
}
@media (hover: hover) {
  .fitting-form__actions-button:hover {
    opacity: 0.4;
  }
  .fitting-form__actions-button:focus {
    opacity: 0.4;
  }
}
@media (hover: none) {
  .fitting-form__actions-button:active {
    opacity: 0.4;
  }
}
.fitting-form__actions-button:disabled {
  opacity: 0.4;
  pointer-events: none;
}
.fitting-form__day-column {
  display: flex;
  flex-direction: column;
  row-gap: 15rem;
}
.fitting-form__day-column-list {
  margin: 0;
  padding: 0;
  display: grid;
  row-gap: 6rem;
  list-style: none;
}
.fitting-form__errors {
  padding: 20rem;
  font-size: 16rem;
  line-height: 1.5;
  background-color: rgba(255, 0, 0, 0.5);
  color: #fff;
}
.fitting-form[hidden] {
  display: none;
}

.field {
  width: 100%;
  display: flex;
  flex-direction: column;
  font-size: 12rem;
  font-weight: 500;
  line-height: 1;
}
.field__control {
  padding-inline: 20rem;
  height: 50rem;
  border: 1rem solid rgba(0, 0, 0, 0.1);
  border-radius: 100rem;
  text-transform: uppercase;
}
.field__control:is(textarea) {
  padding-block: 20rem;
  height: auto;
  border-radius: 20rem;
}
.field__control::placeholder {
  color: rgba(0, 0, 0, 0.3);
}
.field__control[aria-invalid=true] {
  border-color: red;
}
.field__errors {
  margin-top: 5rem;
  padding-inline: 20rem;
  display: flex;
  flex-direction: column;
  row-gap: 5rem;
  color: red;
}
.field__errors:empty {
  display: none;
}

.loveforever-checkbox {
  margin: 0;
  display: flex;
  column-gap: 10rem;
  font-size: 12rem;
  line-height: 1;
  text-transform: uppercase;
}

.accordion__header {
  margin: 0;
}
.accordion__trigger {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding: 20px 0;
  margin: 0;
  border: none;
  background: none;
  text-align: left;
  cursor: pointer;
}
.accordion__trigger:hover .accordion__title {
  opacity: 0.7;
}
.accordion__trigger[aria-expanded=true] .accordion__icon {
  transform: rotate(180deg);
}
.accordion__title {
  font-size: 16rem;
  line-height: 1;
  font-weight: normal;
  transition: opacity 0.2s ease;
  text-transform: uppercase;
}
.accordion__icon {
  margin-left: 12px;
  flex-shrink: 0;
  display: flex;
  justify-content: center;
  transition: transform 0.2s ease;
}
.accordion__panel {
  overflow: hidden;
}
.accordion__panel[hidden] {
  display: none;
}
.accordion__content {
  padding-bottom: 20px;
  display: flex;
  flex-direction: column;
  row-gap: 15rem;
}
.accordion__content p {
  margin: 0;
  font-size: 14px;
  line-height: 1.6;
  color: rgba(0, 0, 0, 0.7);
}

.button {
  height: 50rem;
  padding-inline: 30rem;
  border: 1rem solid var(--purple);
  border-radius: 100rem;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  row-gap: 15rem;
  text-transform: uppercase;
  font-size: 12rem;
  font-weight: 500;
  line-height: 1;
  color: #fff;
  background-color: var(--purple);
  transition: all 0.3s ease;
}
@media (hover: hover) {
  .button:hover {
    background-color: #fff;
    color: var(--purple);
  }
  .button:focus {
    background-color: #fff;
    color: var(--purple);
  }
}
@media (hover: none) {
  .button:active {
    background-color: #fff;
    color: var(--purple);
  }
}
.button--link {
  background-color: transparent;
  border-color: transparent;
  color: #000000;
}
.button--filter {
  padding: 0;
  height: 30rem;
  border-radius: 0;
  background-color: transparent;
  border-color: transparent;
  color: #000000;
}
.button:disabled {
  opacity: 0.6;
  pointer-events: none;
}
.button[hidden] {
  display: none;
}

.btn:disabled {
  opacity: 0.6;
  pointer-events: none;
}

.pagination {
  color: #000;
}
.pagination__list {
  margin: 0;
  padding: 0;
  display: flex;
  list-style: none;
  column-gap: 10rem;
}
.pagination__item {
  display: flex;
  justify-content: center;
  align-items: center;
}
.pagination__link {
  width: 40rem;
  height: 60rem;
  border-radius: 100rem;
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  color: inherit;
  transition: all 0.3s ease;
}
.pagination__link--prev, .pagination__link--next {
  border: 1rem solid rgb(229, 229, 229);
}
@media (hover: hover) {
  .pagination__link:hover {
    background-color: rgb(229, 229, 229);
  }
  .pagination__link:focus {
    background-color: rgb(229, 229, 229);
  }
}
@media (hover: none) {
  .pagination__link:active {
    background-color: rgb(229, 229, 229);
  }
}
.pagination__link--active {
  background-color: var(--pink);
  color: #fff;
  pointer-events: none;
}
@media (hover: hover) {
  .pagination__link--active:hover {
    background-color: var(--pink);
  }
  .pagination__link--active:focus {
    background-color: var(--pink);
  }
}
@media (hover: none) {
  .pagination__link--active:active {
    background-color: var(--pink);
  }
}

.reviews-form {
  display: flex;
  flex-direction: column;
  row-gap: 10rem;
}
.reviews-form[hidden] {
  display: none;
}

.favorites-contact-form {
  gap: 20rem;
}
@media (max-width: 767px) {
  .favorites-contact-form {
    gap: 10rem;
  }
}
.favorites-contact-form__field {
  position: relative;
  color: #fff;
}
.favorites-contact-form__field .field__control {
  border-color: rgba(255, 255, 255, 0.1882352941);
  background-color: transparent;
}
.favorites-contact-form__field .field__control::placeholder {
  color: rgba(255, 255, 255, 0.5019607843);
}
.favorites-contact-form__field .field__control[aria-invalid=true] {
  border-color: red;
}
.favorites-contact-form__field .field__errors {
  position: absolute;
  top: 100%;
  left: 0;
  color: #fff;
}